<template>
  <div class="inventory-container">
    <h2 class="page-title">üì¶ Â∫ìÂ≠òÁÆ°ÁêÜ</h2>

    <!-- ÊêúÁ¥¢ & Á≠õÈÄâ -->
    <div class="filter-container">
      <input v-model="searchQuery" placeholder="üîç ÊêúÁ¥¢ÂïÜÂìÅÂêçÁß∞" class="filter-input" />
      <select v-model="selectedCategory" class="filter-select">
        <option value="">üìç ÊâÄÊúâÁ±ªÂà´</option>
        <option value="ÁîµÂ≠ê‰∫ßÂìÅ">ÁîµÂ≠ê‰∫ßÂìÅ</option>
        <option value="ÂÆ∂ÂÖ∑">ÂÆ∂ÂÖ∑</option>
        <option value="Êó•Áî®">Êó•Áî®</option>
        <option value="È£üÂìÅ">È£üÂìÅ</option>
      </select>
      <button class="glow-button" @click="fetchInventory">üìã Âà∑Êñ∞Êï∞ÊçÆ</button>
    </div>

    <!-- ÂïÜÂìÅÂ∫ìÂ≠òÂàóË°® -->
    <div class="inventory-list">
      <div v-for="item in filteredInventory" :key="item.id" class="inventory-item">
        <div class="inventory-info">
          <h3 class="product-name">{{ item.name }}</h3>
          <p class="product-category">üìç Á±ªÂà´: {{ item.type }}</p>
          <p class="product-stock">üì¶ ÂΩìÂâçÂ∫ìÂ≠ò: {{ item.number }}</p>
        </div>
      </div>
    </div>

    <!-- Â∫ìÂ≠òÁªüËÆ°Âõæ -->
    <div class="chart-container">
      <h3>üìä Â∫ìÂ≠òË∂ãÂäø</h3>
      <div id="stockChart"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import * as echarts from "echarts";

export default {
  data() {
    return {
      inventory: [], // ÊúçÂä°Âô®ËøîÂõûÁöÑÂ∫ìÂ≠òÊï∞ÊçÆ
      searchQuery: "",
      selectedCategory: "",
    };
  },
  computed: {
    filteredInventory() {
      return this.inventory.filter((item) => {
        return (!this.searchQuery || item.name.includes(this.searchQuery)) &&
          (!this.selectedCategory || item.type === this.selectedCategory);
      });
    },
  },
  mounted() {
    this.fetchInventory(); // ËøõÂÖ•È°µÈù¢Êó∂Ëá™Âä®Ëé∑ÂèñÊï∞ÊçÆ
  },
  methods: {
    fetchInventory() {
      this.$http.get("/product/select")
        .then((res) => {
          if (Array.isArray(res.data)) {
            this.inventory = res.data;
            this.$nextTick(() => {
              this.renderStockChart();  // Á°Æ‰øùÊï∞ÊçÆÊõ¥Êñ∞ÂêéÂÜçÊ∏≤ÊüìÂõæË°®
            });
          }
        })
        .catch((error) => {
          console.error("Ëé∑ÂèñÂ∫ìÂ≠òÊï∞ÊçÆÂ§±Ë¥•:", error);
        });
    },
    renderStockChart() {
      let chart = echarts.init(document.getElementById("stockChart"));
      let option = {
        title: {text: "Â∫ìÂ≠òË∂ãÂäø", textStyle: {color: "#fff"}},
        tooltip: {},
        xAxis: {
          type: "category",
          data: this.inventory.map(item => item.name),
          axisLine: {lineStyle: {color: "#fff"}},
        },
        yAxis: {
          type: "value",
          axisLine: {lineStyle: {color: "#fff"}},
        },
        series: [
          {
            name: "Â∫ìÂ≠òÊï∞Èáè",
            data: this.inventory.map(item => Number(item.number)), // Á°Æ‰øùÊï∞ÊçÆ‰∏∫Êï∞Â≠óÁ±ªÂûã
            type: "bar",
            smooth: true,
            itemStyle: {color: "#0ff"},
          },
        ],
      };
      chart.setOption(option);
    }
  },
};
</script>

<style scoped>
/* Ê∑±ËìùÊ∏êÂèòËÉåÊôØ */
.inventory-container {
  background: linear-gradient(135deg, #001f3f, #011f4b);
  color: white;
  min-height: 100vh;
  text-align: center;
  padding: 30px;
}

/* Ê†áÈ¢ò */
.page-title {
  font-size: 28px;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(0, 255, 255, 0.8);
  margin-bottom: 20px;
}

/* Á≠õÈÄâÂå∫Âüü */
.filter-container {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
}

.filter-input, .filter-select {
  padding: 10px;
  border-radius: 8px;
  border: none;
  font-size: 16px;
}

/* ÊåâÈíÆ */
.glow-button {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  background: rgba(0, 255, 255, 0.2);
  color: #0ff;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.5);
  transition: 0.3s;
}

.glow-button:hover {
  background: rgba(0, 255, 255, 0.5);
  box-shadow: 0 0 25px rgba(0, 255, 255, 1);
}

/* ÂïÜÂìÅÂàóË°® */
.inventory-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
  padding: 20px;
}

/* Âçï‰∏™ÂïÜÂìÅÈ°π */
.inventory-item {
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);
  transition: 0.3s;
}

.inventory-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 25px rgba(0, 255, 255, 0.7);
}

/* ÁªüËÆ°Âõæ */
.chart-container {
  margin-top: 30px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);
}

#stockChart {
  width: 100%;
  height: 400px; /* Á°Æ‰øùË∂≥Â§üÁöÑÈ´òÂ∫¶ */
}
</style>



